@import "../settings/color";
@import "../tools/mixin";
@import "../tools/media";

.checkout__social {
  margin-top: 29px; }

.social {
  height: 74px;
  border-radius: 10px;
  background-color: #FBFBFB;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  border: 1px solid #D3D3D3;
  box-sizing: border-box; }

.social__text {
  @include fontSWCL(16px, normal, #000, 20px); }

.social__values {
  display: flex; }

.social__value {
  margin-left: 12px;
  width: 46px;
  height: 44px;
  background-color: #E9E9E9;
  border-radius: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &:hover {
    background-color: $yellow;
    svg path {
      fill: #fff; } } }
